@mixin mobile {
    @media screen and(max-width:768px) {
        @content;
    }
}

.link {
    margin-right: 10px;
    padding: 5px 5px;
    border: none;
    background-color: black;
    color: white;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;

    text-decoration: none;
}
.link:hover {
    background-color: #333;
}
